$font-light: #ffffff;
.ant-layout {
  position: absolute;
  left: 0;
  top: 0;
  background: url("./bg.jpg") fixed no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;

  .ant-layout-header {
    background: transparent;
    backdrop-filter: blur(50px) saturate(150%);
    -webkit-backdrop-filter: blur(50px) saturate(150%);
    color: $font-light;
    font-size: 25px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;

    .ant-select {
      color: $font-light;
    }

    button {
      margin-top: 3px;
    }
  }

  .ant-layout-content {
    background: transparent;
    backdrop-filter: blur(50px) saturate(150%) contrast(50%);
    -webkit-backdrop-filter: blur(50px) saturate(150%) contrast(50%);
    text-align: center;
    overflow: auto;

    .ant-form {
      padding: 10px;

      .ant-form-item-required {
        color: $font-light
      }
    }
  }

  .ant-layout-footer {
    text-align: center;
  }
}